// ()key
function getValueOfItem(key, array, callback) {
  const item = array.find(callback);
  return item[key];
}

// 
function isEmptyArray(array) {
  return array.length === 0;
}

// 
function isArray(data) {
  return Object.prototype.toString.call(data) === '[object Array]'
}

export {
  isEmptyArray,
  getValueOfItem,
  isArray
};
